.heroimage {
    background-size: 100%, cover !important;
    background-position: center, center !important;
    width: 100%;
    height: 600px;
    position: relative;
    animation: animateHeroimage 1s;
}

@keyframes animateHeroimage {
    from {
        opacity:0;
    }
    to {
        opacity:1;
    }
}

.heroimage-content {
    max-width: 1280px;
    height: -webkit-fill-available;
    padding: 20px;
    margin: 0 auto;
}

.heroimage-text {
    z-index: 100;
    max-width: 700px;
    position: absolute;
    bottom: 40px;
    margin-right: 20px;
    min-height: 100px;
    background: rgba(0, 0, 0, 0.0);
    color: #fff;
}

.heroimage-text h1 {
    font-family: 'Abel', sans-serif;
    font-size:48px;
    color: #fff;
}

.heroimage-text p {
    font-family: 'Abel', sans-serif;
    font-size: 22px;
    line-height: 26px;
    color: #fff;
}

@media screen and (max-width: 720px) {
    .heroimage-text {
        max-width: 100%;
    }

    .heroimage-text h1 {
        font-size: 38px;
        color: #fff;
    }

    .heroimage-text p {
        font-size: 16px;
        line-height: 20px;
        color: #fff;
    }
}