.header {
    width: 100%;
    height: auto;
    background: #1c1c1c;
    padding: 0 20px;
    box-sizing: border-box;
}

.header-content {
    max-width: 1280px;
    min-height: 120px;
    height: auto;
    padding: 20px 0px;
    margin: 0 auto;
    box-sizing: border-box;
    overflow: hidden;
}

.logo {
    width: 300px;
    margin-top: 20px;
    float: left;
}

@media screen and (max-width: 500px) {
    .header-content {
        max-width: 1280px;
        min-height: 0px;
    }

    .logo {
        width: 150px;
        margin-top: 5px;
    }
}